import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import  stores  from './stores'
import { Provider } from 'react-redux'
import App from './App';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <Provider store={stores}>
    <App />
    </Provider>
  </React.StrictMode>
);
